import Link from "next/link";
import theme from "../styles/theme";

export default function ProductList(props) {
  const rows = [];
  let row = [];
  const columns = Math.max(2, Math.min(4, props.columns));
  props.items.forEach((item) => {
    if (row.length === columns) {
      rows.push(row);
      row = [];
    }
    row.push(item);
  });
  if (row.length > 0) {
    rows.push(row);
  }

  const fullWidth = `${100 / (columns + 1)}%`;

  return (
    <React.Fragment>
      <h2
        style={{
          textAlign: "center",
          fontFamily: theme.fonts.sectionTitle,
          fontWeight: theme.fonts.productTitleWeight,
        }}
      >
        {props.title}
      </h2>
      {rows.map((row, indexRow) => {
        return (
          <div className="rootWrapper" key={indexRow}>
            <div className="sectionsWrapper">
              {row.map((item, index) => {
                return (
                  <Link href={item.href} key={index}>
                    <a className="item">
                      <img src={item.img} alt={item.imgAlt} />
                      <h3 className="link">{item.title}</h3>
                      {item.salePrice && (
                        <div className="link sale">{item.salePrice} SALE</div>
                      )}
                      <div
                        className={
                          item.salePrice ? "link crossedOut" : "link price"
                        }
                      >
                        {item.price}
                      </div>
                      {item.shippedInBox && (
                        <React.Fragment>
                          <div className="link shippedInBox">
                            <svg
                              viewBox="0 0 86.03 73.22"
                              style={{
                                marginTop: "15px",
                                width: "15px",
                                height: "15px",
                                stroke: theme.colors.shippedInBox,
                              }}
                            >
                              <path d="M85.77 10.17a2.5 2.5 0 0 0-1.64-1.34L49.33.08a2.5 2.5 0 0 0-2.89 1.4l-8.43 18.68-5.58 3.32-29.32-7.37A2.5 2.5 0 0 0 .35 19.8L11.2 38.23v23.73a2.5 2.5 0 0 0 1.89 2.42l34.8 8.75a2.49 2.49 0 0 0 .61.08 2.62 2.62 0 0 0 .78-.13l.2-.08 26.53-15.9a2.51 2.51 0 0 0 1.21-2.14V31.2l8.55-18.92a2.5 2.5 0 0 0 0-2.11zM15.31 35.38l-7.63-13 28 7 7.63 13zm.88 24.63V40.88l29.8 7.5v19.13zm50.12-13v-8.4l5.94-3.57V53.5L51.01 66.3V47.84l6.91-4.15v8.52zM42.46 25.1l25.85 6.5-2 1.2v-.12l-7.21 4.45-9.68 5.81-7-11.83zm1-4.9l6.67-14.78 29.88 7.53-6.68 14.78z"></path>
                            </svg>
                            Shipped in a Box
                          </div>
                        </React.Fragment>
                      )}
                    </a>
                  </Link>
                );
              })}
            </div>
          </div>
        );
      })}

      <style jsx>{`
        .rootWrapper {
          display: flex;
          align-items: center;
          justify-content: space-around;
          margin: 0 -7px;
        }

        .sectionsWrapper {
          display: flex;
          align-items: center;
          justify-content: center;
          flex-wrap: wrap;
          text-align: center;
        }

        .item {
          text-decoration: none;

          color: ${theme.colors.productTitle};
        }

        @media screen and (max-width: 768px) {
          .item {
            width: 100%;
          }
        }

        @media screen and (min-width: 768px) and (max-width: 1240px) {
          .item {
            width: 50%;
          }
        }

        @media screen and (min-width: 1240px) {
          .item {
            width: ${fullWidth};
            margin: 0 20px;
          }
        }

        .link {
          margin: 10px;
          text-align: center;
          font-family: ${theme.fonts.link};
          font-weight: ${theme.fonts.productNameWeight};
          font-size: ${theme.fonts.productNameSize};
          font: ${theme.fonts.productListFont};
        }

        .sale {
          color: ${theme.colors.red};
        }

        .price {
          color: ${theme.colors.grey};
        }

        .crossedOut {
          color: ${theme.colors.grey};
          font: ${theme.fonts.productListFont–°rossedOut};
          text-decoration: line-through;
        }

        .shippedInBox {
          color: ${theme.colors.shippedInBox};
          font: ${theme.fonts.productShippedInBox};
        }
      `}</style>
    </React.Fragment>
  );
}
